.titulo {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 60%;
  height: fit-content;
  top: 3vh;
  right: 0;
  bottom: 0;
  left: 0;
  margin: 0 auto;
}
.titulo h2 {
  font-family: "Josefin Slab", serif;
  font-size: 3rem;
  font-weight: 100;
  color: #584528;
}
.titulo h3 {
  font-size: 1rem;
  font-weight: 200;
  letter-spacing: 0.5rem;
  color: #584528;
}
.titulo h3 .wpp {
  position: relative;
  background-color: transparent;
  padding: 0.1rem 0.1rem;
  width: fit-content;
  height: 3rem;
  border: none;
  overflow: hidden;
  transition: all 0.5s ease;
  cursor: pointer;
  z-index: 10;
}
.titulo h3 .wpp a {
  width: 100%;
  height: 100%;
  text-decoration: none;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 1.2rem;
  color: #333;
  transition: all 0.5s ease;
  margin: 0;
}
.titulo h3 .wpp::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0%;
  height: 0.1rem;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 1.5rem;
  color: #fff;
  background-color: #333;
  transition: all 0.5s ease;
}
.titulo h3 .wpp:hover::after {
  width: 100%;
  transition: all 0.5s ease;
}
.titulo h2, .titulo h3 {
  color: #333;
  z-index: 1;
}
@media (min-width: 480px) {
  .titulo h2 {
    font-size: 3.5rem;
  }
}
@media (min-width: 768px) {
  .titulo {
    top: 3vh;
    width: 100%;
  }
  .titulo h2 {
    font-size: 4.5rem;
  }
  .titulo h3 {
    font-size: 1.5rem;
    letter-spacing: 0.8rem;
  }
