    font-size: 6rem;
  }
}

.prox {
  position: absolute;
  top: 15vh;
  left: 49%;
  text-align: center;
  color: #f9f5f1;
  border-radius: 50%;
  padding: 1rem 1.1rem;
  transition: 0.3s;
  cursor: pointer;
  z-index: 20;
}
.prox:hover {
  transform: scale(1.1);
}

.button {
  position: absolute;
  padding: 1rem 1.1rem;
  top: calc(25vh - 25px);
  left: calc(50vw - 25px);
  border: none;
  outline: none;
  border-radius: 50%;
  background-color: #333;
  color: #f9f5f1;
  cursor: pointer;
  transition: all 0.3s ease-out;
  z-index: 10;
}
.button::after {
  content: "";
  display: block;
  position: absolute;
  width: 4rem;
  height: 4rem;
  background-color: rgb(65, 65, 65);
  z-index: -1;
  left: calc(50% - 20px);
  top: 10px;
  border-radius: 50%;
  opacity: 0.3;
  filter: blur(5px);
  transition: all 0.3s ease-out;
}
.button:hover::after {
  opacity: 0.5;
  filter: blur(20px);
  transform: translatey(10px) scalex(1.1);
}
.button:active {
  background-color: #534231;
}
.button:active::after {
  opacity: 0.3;
}
.button.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  width: 4rem;
  height: 4rem;
  left: calc(50vw - 25px);
  transition: all 0.3s ease-out;
}
.button.loading::after {
  width: 40px;
  left: 5px;
  top: 12px;
  border-radius: 100%;
  transition: all 0.3s ease-out;
}
.button .spinner {
  display: block;
  width: 3.2rem;
  height: 3.2rem;
  position: absolute;
  top: 5px;
  left: calc(50% - 16px);
  background: transparent;
  box-sizing: border-box;
  border-top: 4px solid white;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 4px solid transparent;
  border-radius: 100%;
  animation: spin 0.6s ease-out infinite;
  transition: all 0.3s ease-out;
}
@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

