@use 'base/variables' as v;
@use 'base/mixins' as m;

@mixin transition($property: all, $duration: 0.45s, $ease: cubic-bezier(0.65,0,.076,1)) {
    transition: $property $duration $ease;
  }

.caja-img {
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;

    img {
        pointer-events: none;
    }

    .layer {
        position: absolute;
        width: 100%;
        height: 100vh;
        background-size: cover;
    }
    
    #fondo1 {
      left: -3rem;

      @include m.tablet {
        left: -4rem;
      }
      @include m.desktop {
        left: -10rem;
      }
      @include m.desktopxl {
        left: -20rem;
      }
    }
    #fondo2 {
      left: -1rem;

      @include m.tablet {
        left: -3rem;
      }
      @include m.desktop {
        left: -8rem;
      }
      @include m.desktopxl {
        left: -18rem;
      }
    }

    #fondo3 {
      right: -3rem;
      bottom: 4rem;
      z-index: 1;

      @include m.tablet {
        right: -6rem;
        bottom: 8rem;
      }
      @include m.desktop {
        right: -10rem;
        bottom: 10rem;
      }
      @include m.desktopxl {
        right: -20rem;
      }
    }

    #fondo4 {
      bottom: -2rem;
      right: -4rem;

      @include m.tablet {
        right: -8rem;
      }
      @include m.desktop {
        right: -12rem;
        bottom: -3rem;
      }
      @include m.desktopxl {
        right: -28rem;
      }
    }
}

.logo {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 2%;
  left: 2%;
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  background-color: #534231;
  font-family: v.$fuente_card_minuscula;
  font-weight: 600;
  font-size: 1.5rem;
  // text-transform: uppercase;
  letter-spacing: -.1rem;

  @include m.telefono {
    width: 8rem;
    height: 8rem;
    font-size: 1.8rem;
  }

  @include m.tablet {
    width: 10rem;
    height: 10rem;
    font-size: 2.5rem;
  }

  @include m.desktop {
    width: 12rem;
    height: 12rem;
    font-size: 2.8rem;
  }
}

.titulo {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 60%;
    height: fit-content;
    top: 3vh;
    right: 0;
    bottom: 0;
    left: 0;
    margin:0 auto;

    h2 {
        font-family: v.$fuente_card_minuscula;
        font-size: 3rem;
        font-weight: 100;
        color: v.$marron;
    }

    h3 {
        font-size: 1rem;
        font-weight: 200;
        letter-spacing: .5rem;
        color: v.$marron;

    .wpp {
            position: relative;
            background-color: transparent;
            padding: .1rem .1rem;
            width: fit-content;
            height: 3rem;
            border: none;
            overflow: hidden;
            transition: all .5s ease;
            cursor: pointer;
            z-index: 10;

            a {
                width: 100%;
                height: 100%;
                text-decoration: none;
                font-family: v.$fuente_principal;
                font-weight: 700;
                font-size: 1.2rem;
                color: #333;
                transition: all .5s ease;
                margin: 0;
            }

            &::after {
                content: '';
                display: block;
                position: absolute;
                bottom: 0;
                left: 0;
                width: 0%;
                height: .1rem;
                font-family: v.$fuente_principal;
                font-weight: 700;
                font-size: 1.5rem;
                color: #fff;
                background-color: #333;
                transition: all .5s ease;
            }

            &:hover {
                &::after {
                    width: 100%;
                    transition: all .5s ease;
                }
            }
        }
    }

    h2, h3 {
            color: v.$negro;
            z-index: 1;
    }

    @include m.telefono {
      h2 {
        font-size: 3.5rem;
      }
    }

    @include m.tablet {
      top: 3vh;
      width: 100%;

      h2 {
        font-size: 4.5rem;
      }

      h3 {
        font-size: 1.5rem;
        letter-spacing: .8rem;

        .wpp {
          padding: .5rem .5rem;
          a {
            font-size: 1.5rem;
          }
        }
      }

    }

    @include m.desktop {
      top: 5vh;

      h2 {
        font-size: 6rem;
      }
    }
}


.prox {
  position: absolute;
  top: 15vh;
  left: 49%;
  text-align: center;
//   background-color: #534231;
  color: v.$crema;
  border-radius: 50%;
  padding: 1rem 1.1rem;
  transition: .3s;
  cursor: pointer;
  z-index: 20;

  &:hover {
    transform: scale(1.1);
  }
}

.button {
    position: absolute;
    // top: 25vh;
    // left: 49%;
    padding: 1rem 1.1rem;
    top: calc(25vh - 25px);
    left: calc(50vw - 25px);
    border: none;
    outline: none;
    border-radius: 50%;
    // background-color: #534231;
    background-color: #333;
    color: v.$crema;
    cursor: pointer;
    transition: all 0.3s ease-out;
    z-index: 10;

    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 4rem;
      height: 4rem;
      background-color: rgb(65, 65, 65);
      z-index: -1;
      left: calc(50% - 20px);
      top: 10px;
      border-radius: 50%;
      opacity: 0.3;
      filter: blur(5px);
      transition: all 0.3s ease-out;
    }

    &:hover::after {
      opacity: 0.5;
      filter: blur(20px);
      transform: translatey(10px) scalex(1.1);
    }

    &:active {
      background-color: #534231;
    }

    &:active::after {
      opacity: 0.3;
    }

    &.loading {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50px;
      width: 4rem;
      height: 4rem;
      left: calc(50vw - 25px);
      transition: all 0.3s ease-out;

      &::after {
        width: 40px;
        left: 5px;
        top: 12px;
        border-radius: 100%;
        transition: all 0.3s ease-out;
      }
    }

    .spinner {
      display: block;
      width: 3.2rem;
      height: 3.2rem;
      position: absolute;
      top: 5px;
      left: calc(50% - 16px);
      background: transparent;
      box-sizing: border-box;
      border-top: 4px solid white;
      border-left: 4px solid transparent;
      border-right: 4px solid transparent;
      border-bottom: 4px solid transparent;
      border-radius: 100%;
      animation: spin 0.6s ease-out infinite;
      transition: all 0.3s ease-out;
    }

    @keyframes spin {
      100% {transform: rotate(360deg)}
    }
  }

