.container {
    position: absolute;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    background-color: transparent;

    .slider {
        position: absolute;
        top: 15vh;
        width: 100vw;
        height: 90vh;
        overflow: hidden;
        perspective: 200px;
        perspective-origin: 50% 100%;

        .card {
            position: absolute;
            top: 50%;
            left: 50%;
            display: flex;
            flex-direction: column;
            width: 60%;
            height: 350px;
            border-bottom-right-radius: 2rem;
            border-top-left-radius: 2rem;
            transform: translate3d(-50%, -50%, 0);
            background-color: v.$blanco;
            -webkit-box-shadow: -3px 21px 42px -10px rgba(0,0,0,0.65);
            -moz-box-shadow: -3px 21px 42px -10px rgba(0,0,0,0.65);
            
            @include m.tablet {
              flex-direction: row;
            }

            @include m.desktop {
            width: 45%;
            }

            @include m.desktopxl {
              width: 35%;
              }

            &.inciensos {
              background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url('/imagenes/sieteChakras.jpg');
              // background-image: url('/imagenes/sieteChakras.jpg');
              background-size: cover;
              background-repeat: no-repeat;
            }

            &.sahumos {
              background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url('/imagenes/sahumos1.jpeg');
              // background-image: url('/imagenes/sieteChakras.jpg');
              background-size: cover;
              background-repeat: no-repeat;
            }

            &.defumacion {
              background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url('/imagenes/defumacion.jpeg');
              // background-image: url('/imagenes/sieteChakras.jpg');
              background-size: cover;
              background-repeat: no-repeat;
            }

            &.kits {
              background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url('/imagenes/kither.jpg');
              // background-image: url('/imagenes/sieteChakras.jpg');
              background-size: cover;
              background-repeat: no-repeat;
            }

            &.aromaterapia {
              background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url('/imagenes/perla.jpg');
              // background-image: url('/imagenes/sieteChakras.jpg');
              background-size: cover;
              background-repeat: no-repeat;
            }

            &.inciensos, &.sahumos, &.defumacion, &.kits, &.aromaterapia {
              @include m.tablet {
                background: v.$blanco;
              }
            }

            .producto {
                display: none;
                position: absolute;
                top: -2rem;
                right: -2rem;
                width: 60%;
                height: 90%;
                border-bottom-left-radius: 3rem;
                background-color: transparent;
                overflow: hidden;

                p {
                    position: relative;
                    display: block;
                    width: 100%;
                    height: 100%;
                    margin: 0;
                    padding: 0;
                    filter: grayscale(.1);
                    background-size: cover;
                    background-position: center center;
                    border-bottom-left-radius: 3rem;
                    cursor: pointer;
                    transform: scale(1);
                    transition: all .1s;
                }

                @include m.tablet {
                  display: block;
                }

                #inciensos {
                    background-image: url('/imagenes/natural.webp');
                }

                #sahumos {
                    background-image: url('/imagenes/sahumos3.jpg');
                }

                #defumacion {
                    background-image: url('/imagenes/herbal.jpg');
                }

                #kits {
                    background-image: url('/imagenes/kitluz.jpg');
                }

                #aroma {
                    background-image: url('/imagenes/aroma.jpg');
                }
            }

            .contenido {
                // position: relative;
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: flex-start;
                margin: 0;
                padding: 3rem 2rem 0 2rem;

                .copy {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    width: 100%;
                    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
                    text-align: center;
    
                    h1{
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        justify-content: center;
                        width: 100%;
                        text-align: center;
                        font-size: 5vw;
                        font-weight: 600;
                        text-transform: uppercase;
                        color: v.$crema;
                        font-family: v.$fuente_card_minuscula;
                        // margin: 0 auto;
                        // padding: 0;
    
                        // &.uno {
                        //     font-size: 1.2vw;
                        // }
    
                        
                        span {
                            // display: inline-block;
                            display: flex;
                            flex-direction: row;
                            align-items: center;
                            justify-content: center;
                            color: v.$blanco;
                            font-family: v.$fuente_card_minuscula;
                        }

                        @include m.telefono {
                          font-size: 3.5vw;
                        }

                        @include m.tablet {
                          font-size: 2.5vw;
                          font-weight: 200;
                          color: v.$negro;

                          span {
                            color: v.$negro;
                          }
                        }

                        @include m.desktop {
                          font-size: 1.8vw;
                        }

                        @include m.desktopxl {
                          font-size: 1.5vw;
                        }
                    }
                }
    
                .losp {
                    width: 100%;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: space-between;
                    text-align: center;
                    background-color: #00000088;
                    border-radius: 2rem;
                    z-index: 90;
                    padding: 1rem 0;
    
                    p {
                        font-family: v.$fuente_principal;
                        position: relative;
                        font-size: 1rem;
                        font-weight: 300;
                        color: v.$crema;
                        margin: 0;
                        padding: 0.3rem;
                        border-radius: .5rem;
    
                        span {
                            font-weight: 500;
                        }
                    }

                    @include m.tablet {
                      background-color: #f3eeea;

                      p {
                        color: #444;
                      }
                    }
                }

                @include m.telefono {
                }

                @include m.tablet {
                  width: 45%;
                }

                @include m.desktop {
                  
                }
            }

            .poton {
                position: absolute;
                bottom: 1rem;
                right: 1rem;
                display: inline-block;
                cursor: pointer;
                outline: none;
                border: 0;
                vertical-align: middle;
                text-decoration: none;
                background: transparent;
                padding: 0;
                font-size: inherit;
                font-family: inherit;

                &.learn-more {
                  width: 9rem;
                  height: auto;
                  .circle {
                    @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
                    position: relative;
                    display: block;
                    margin: 0;
                    width: 2.5rem;
                    height: 2.5rem;
                    background: #333;
                    border-radius: 1.625rem;
                    .icon {
                      @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
                      position: absolute;
                      top: 0;
                      bottom: 0;
                      margin: auto;
                      background: #fff;
                      &.arrow {
                        @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
                        left: 0.400rem;
                        width: 1.125rem;
                        height: 0.125rem;
                        background: none;
                        &::before {
                          position: absolute;
                          content: '';
                          top: -0.24rem;
                          right: 0.0625rem;
                          width: 0.625rem;
                          height: 0.625rem;
                          border-top: 0.125rem solid #fff;
                          border-right: 0.125rem solid #fff;
                          transform: rotate(45deg);
                        }
                      }
                    }
                  }
                  .button-text {
                    @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
                    position: absolute;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    left: 0;
                    padding: .40rem 0 0 1rem;
                    margin: 0 0 0 1rem;
                    color: #333;
                    font-size: 1rem;
                    font-weight: 700;
                    line-height: 1.6;
                    text-align: center;
                    text-transform: uppercase;
                  }
                }
                &:hover {
                  .circle {
                    width: 100%;
                    .icon {
                      &.arrow {
                      background: #fff;
                      transform: translate(1rem, 0);
                      }
                    }
                  }
                  .button-text {
                    color: #fff;
                  }
                }
              }
        }
    }
}

@media(max-width: 900px) {
    .pages {
        display: none;
    }
}

